.sidebar {
    h1{
        color: #1f1f1f;
        font-size: 1.3rem   ;
    }
    padding: 20px 0 50px 20px;
.menu-items{
    padding: 20px 60px 20px 0px;

  .category-section {
    margin-bottom: 2rem;

    .category-name {
      font-size: 1rem;
      line-height: 20px;
      font-weight: 600;
      color: #1f1f1f;
      margin-bottom: 1rem;
    }

    .items-list {
      display: grid;
      grid-template-columns: repeat(6, 1fr); // 5 items per row on desktop

      @include media('xl') { // Adjust for 1028px
        grid-template-columns: repeat(5, 1fr); // 5 items per row
      }
      @include media('lg') { // Adjust for mobile
        grid-template-columns: repeat(4, 1fr); // 2 items per row on mobile
      }
      @include media('md') { // Adjust for mobile
        grid-template-columns: repeat(3, 1fr); // 2 items per row on mobile
      }
      @include media('sm') { // Adjust for mobile
        grid-template-columns: repeat(2, 1fr); // 2 items per row on mobile
      }
    }
    
  }
}
}