@import '../variables'; // Adjust path as needed

.menu-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  border-radius: $border-radius;
  margin: $margin;
  transition:transform 0.3s;
  width: 176px; // Adjust width as needed
  height: 106px; // Adjust height as needed

  &:hover {
    box-shadow: 0 6px 12px darken($shadow-color, 10%);
    transform: translateY(-5px);
  }

  .image-container {
    position: relative; // Allows positioning of price
    width: 100%;
    height: 64px; // Adjust based on design
    overflow: hidden;
    border-radius: 8px 8px 0  0;

    img {
      width: 176px;
      height: 64px; // Adjust based on design
      object-fit: cover;
    }

    .price {
      position: absolute;
      top: -8px;
      left: 5px; // Adjust position from left
      background-color: #ffffff; // Adjust background color as needed
      color: #000000;
      padding: 6px 12px;
      border-radius: $border-radius;
      font-size: 0.9rem; // Adjust font size as needed
      font-weight: bold;
    }
  }

  .details {
    text-align: center;
    margin: 10px 0;

    .name {
      font-size: 11px; // Adjust font size as needed
      font-weight: 500; // Semi-bold font weight
      color: #1f1f1f; // Ensure $text-color is defined
      margin: 0;
      line-height: 14px;
    }
  }
}
